.main {
    background-color: $color-mine-shaft;
    color: white;
    padding-top: 20px;
    padding-bottom: 10px;
}

.main__header {
    text-align: center;
    margin: 0;
    margin-bottom: 20px;
}

.main__img-wrapper {
    position: relative;
}

.main__img-desc {
    position: absolute;
    bottom: 3px;

    margin: 0;
    padding: 5px;

    color: white;
    text-shadow: 1px 1px 2px black;
    background-color: rgba($color: #000000, $alpha: 0.5);
}

.main__desc {
    &--green {
        color: $color-green;
    }

    &--blue {
        color: $color-dodger-blue;
    }

    &--red {
        color: $color-grenadier;
    }
}

.main__pin-wrapper {
    position: relative;
}

.main__img {
    width: 100%;
}

.main__pin {
    display: block;
    position: absolute;
    top: 30%;
    left: 33%;

    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: $color-green;

    & svg {
        transform: scale(0.9);
        fill: $color-black;
    }

    &--second {
        top: 43%
    }

    &--third {
        top: 55%
    }
    
    &--fourth {
        left: 62%;
        top: 38%;
    }

    &--fifth {
        left: 62%;
        top: 47%;
    }

    &--sixth {
        left: 62%;
        top: 57%;
    }

    &--reserved {
        background-color: $color-dodger-blue;
    }

    &--sold {
        background-color: $color-grenadier;
    }
}

.main__images {
    display: flex;
    justify-content: space-between;

    margin-top: 5px;

    & img {
        width: 33%;
        border: none;
        background-color: transparent;
    }
}

@media (min-width: $desktop-width) {
    .main {
        margin-left: 19%;
    }
}