.header {
    background-color: $color-san-juan;
    position: relative;
}

.header__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding-top: 20px;
    padding-bottom: 20px;
}

.header__logo {
    display: block;
}

.header__menu-btn {
    display: block;
    position: relative;

    height: 50px;
    width: 50px;
    border: none;
    background-color: transparent;

    &::before,
    &::after {
        content: "";
        width: 40px;
        height: 4px;
        background-color: white;
        position: absolute;
        left: 5px;
        top: 20px;
    }

    &:hover {
        &::before,
        &::after {
            background-color: $color-grenadier;
        } 
    }

    &::after {
        top: 30px;
    }

    &--opened {
        &::before {
            transform: rotate(45deg);
            top: 25px;
        }

        &::after {
            transform:  rotate(-45deg);
            top: 25px;
        }
    }

    &--no-js {
        display: none;
    }
}

.header__navigation {
    display: none;

    &--opened {
        display: block;
        position: absolute;
        
        bottom: -200px;
        left: 0;
        right: 0;
        z-index: 2;

        padding-left: 20px;
        padding-top: 20px;

        background-color: $color-san-juan;
    }

    &--no-js {
        display: block;
        color: white;
    }
}

.header__navigation-list {
    @include clear-list;
}

.header__navigation-item {
    margin-bottom: 25px;

    & a {
        @include clear-link;
        display: block;
        width: 100%;
        color: white;
        font-size: 26px;
    }
}

.header__social-list {
    @include clear-list;

    display: flex;
    justify-content: space-around;
    margin-bottom: 20px;
}

@media (min-width: $desktop-width) {
    .header {
        position: fixed;
        z-index: 3;
        top: 0;
        left: 0;
        bottom: 0;
        width: 19%;
        background-color: $color-san-juan;
    }

    .header__menu-btn {
        display: none;
    }

    .header__navigation {
        display: block;
        margin-top: 40px;
        border-bottom: 1px solid white;
    }

    .header__wrapper {
        flex-direction: column;
    }
}