.contact {
    display: flex;
    justify-content: space-around;
}

.contact__text {
    width: 45%;
    font-size: 22px;
    text-align: center;
}

.contact__tel {
    @include clear-link;

    display: flex;
    align-items: center;

    padding-left: 40px;
    background: url('../img/tel.png') no-repeat center left;
    background-size: 20%;
}